import "./App.css";
import Countdown from "react-countdown";

import ceremony from "./assets/ceremony.png";
import party from "./assets/party.png";
import Location from "./assets/Locatie.png";

const date = new Date("2023-07-14T12:00:00Z");

function Details() {
  const renderer = ({ days, hours, minutes, seconds, completed }) => {
    if (completed) {
      return <span>A inceput petrecerea</span>;
    } else {
      // Render a countdown
      return (
        <span className="countdown-timer">
          {days} de zile, {hours} {hours === 1 ? "oră" : "ore"} si {minutes}{" "}
          {minutes > 20 ? "de minute" : "minute"}
        </span>
      );
    }
  };
  return (
    <div className={`App  movie-finished`}>
      <div className="leaves-container"></div>

      <div className="details">
        <div>
          <p className="countdown">Au mai rămas:</p>
          <Countdown date={date} renderer={renderer} />
        </div>
        <div className="program">
          <div>
            <img className="icon" alt="Ceremonie" src={ceremony} /> <br />
            <span className="program-title">Ceremonie</span> <br />{" "}
            <span className="program-text">
              Începând cu <br /> ora 14:00
            </span>
          </div>
          <span className="separator"></span>
          <div>
            <img className="icon" alt="Party" src={party} /> <br />
            <span className="program-title">Petrecerea</span> <br />{" "}
            <span className="program-text">
              Începând cu <br /> ora 17:00
            </span>
          </div>
        </div>
        <div className="description">
          <b>
            {" "}
            Evenimentul va avea loc integral în aer liber. Ne dorim să vă
            ridicăm de la masă și să dansăm până în zori, astfel vă propunem o
            ținută în care să vă simțiți cât mai confortabil.
          </b>
        </div>

        <div className="description description-2">
          Pentru cei ce vin de pe alte meleaguri, vom oferi cazare în Suceava și
          transport către locație, aceasta fiind la 15 km distanță de oraș.
        </div>

        <div className="description">
         
          <a href="https://goo.gl/maps/V1YGs5xG4TYcihdn7" target="_blank" rel="noreferrer">
           <img className="location" alt="Locatie" src={Location} />
          </a>
        </div>
      </div>
    </div>
  );
}

export default Details;
