import { useState, useRef, useEffect } from "react";
import movie from "./assets/video.mp4";
import movieMob from "./assets/video-mobile.mp4";
import audio from "./assets/audio.mp3";

import playBtn from "./assets/play.png";
import date from "./assets/date.png";
import cta from "./assets/CTA.png";
import { useNavigate } from "react-router-dom";

import "./App.css";

function App() {
  const movieRef = useRef(null);
  const videoContainerRef = useRef(null);
  const [videoswitch, setvideo] = useState(false);
  const [toggleClass, setToggleClass] = useState(false);
  const navigate = useNavigate();

  const [names, setNames] = useState([""]);
  const [cazare, setCazare] = useState(false);
  const [textarea, setTextarea] = useState("");

  const [popup, setPopup] = useState(false);

  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }

  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  const isMobile = width <= 768;

  const handleVideo = () => {
    if (movieRef.current.paused) {
      const audioPlayer = new Audio(audio);
      audioPlayer.loop = true;
      audioPlayer.play();

      movieRef.current.play();
      setvideo(true);
    } else {
      movieRef.current.pause();
      setvideo(false);
    }
  };

  const updateNames = (e, index) => {
    const updatedNames = [...names];
    updatedNames[index] = e.target.value;
    setNames(updatedNames);
  };

  const submit = () => {
    console.log(names);
    console.log(cazare);
    console.log(textarea);

    const headers = new Headers();

    headers.append("Content-Type", "application/json");
    headers.append("Accept", "application/json");

    headers.append("key", "lTrPCfX7n7TZ2eNR");
    headers.append(
      "value",
      "1AW73GtMfQ5eHaBnGrfVKQlRGnq3eTPQtbbqspSjJ3aatPeSg7EWghhXsLYE9RLo"
    );
    headers.append("workspaceId", "9c7ae92e-12bb-47ac-9c8d-07e49763a85b");

    fetch(
      "https://webapi.procesio.app/api/Projects/e7bfdf6a-8caf-4171-8af9-27f621d1434d/run",
      {
        method: "POST",
        headers: headers,
        body: JSON.stringify({
          payload: {
            input: {
              names: names.join(", "),
              cazare: cazare ? "Da" : "Nu",
              textarea: textarea,
            },
          },
          connectionId: "",
        }),
      }
    )
      .then((response) => response.json())
      .then((result) => {
        console.log("Success:", result);
        navigate("/detalii");
      });
  };

  const handleEnd = () => {
    setTimeout(() => {
      console.log(movieRef.current);
      movieRef.current.webkitExitFullScreen();
      setToggleClass(true);
    }, 2000);
    setTimeout(() => {
      videoContainerRef.current.style.display = "none";
    }, 2600);
  };

  return (
    <div className={`App ${toggleClass && "movie-finished"}`}>
      <div className="leaves-container"></div>
      <div className="video-container" ref={videoContainerRef}>
        <video onEnded={handleEnd} ref={movieRef} muted className="video">
          <source src={isMobile ? movieMob : movie} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
        <div onClick={handleVideo} className="play">
          {!videoswitch ? <img alt="play" src={playBtn} /> : null}
        </div>
      </div>
      <div className="invitation-container">
        <p className="invite-intro">
          Vă invităm cu bucurie să ne fiți alături în ziua căsătoriei noastre,
        </p>
        <img className="invite-date" src={date} alt="14 iulie 2023" />
        <p className="invite-location">
          Locația evenimentului este{" "}
          <a
            target="_blank"
            rel="noreferrer"
            href="https://www.antiquebucovina.ro/galerie-foto"
          >
            Antique Garden
          </a>
          , lângă pădurea de fagi din Dragomirna.
          <br /> Așteptăm cu nerăbdare să împărtășim bucuria împreună!
        </p>

        <p className="invite-rsvp">
          Vă rugăm să ne confirmați prezența până la dată de <br /> 1 iunie 2023
        </p>

        <img
          alt=""
          className="invite-cta"
          src={cta}
          onClick={() => setPopup(true)}
        />

        {popup && (
          <div className="popup-container">
            <div className="popup">
              <span className="popup-close" onClick={() => setPopup(false)}>
                X
              </span>
              <div className="popup-title">RSVP</div>
              <div className="popup-form">
                {names.map((name, index) => {
                  return (
                    <div key={index} className="popup-form-row">
                      <input
                        placeholder="Nume Prenume"
                        className="popup-form-input"
                        value={name}
                        onChange={(e) => updateNames(e, index)}
                      />
                      {index > 0 && (
                        <span
                          className="popup-form-remove"
                          onClick={() =>
                            setNames(names.filter((_, i) => i !== index))
                          }
                        >
                          -
                        </span>
                      )}
                    </div>
                  );
                })}
                <div
                  className="popup-form-add"
                  onClick={() => setNames([...names, ""])}
                >
                  Adaugă invitați
                </div>
                <div className="popup-form-row popup-checkbox">
                  <input
                    type={"checkbox"}
                    value={cazare}
                    onChange={(e) => setCazare(e.target.checked)}
                  />{" "}
                  Doresc cazare
                </div>

                <div className="popup-form-row">
                  <textarea
                    placeholder="restricţii / preferinţe alimentare
                    (ex: lactate, gluten, vegan etc.)"
                    value={textarea}
                    onChange={(e) => setTextarea(e.target.value)}
                    rows={4}
                  />
                </div>

                <div className="popup-submit" onClick={submit}>
                  Trimite
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
